<template>
  <div>
    <NotAuth>
      <template v-slot:error></template>
      <template v-slot:loading></template>
      <template v-slot:default>
        <div class="chat__wrapper d-flex flex-row">
          <div style="margin-left: 20px; margin-top: 80px">
            <Panel />
          </div>
          <v-card class="chat" style="margin-top: 80px">
            <ChatUsers @dataEdit="dataEdit" @isModalFalse="isModalFalse" :isOpen="isOpen" :chatData="chatData" />
            <v-btn @click="elevator.elevate()" large color="success" class="chat__btn">
              <v-icon>vertical_align_top</v-icon>
            </v-btn>
            <v-toolbar class="chat__top">
              <v-card-title>{{ chatData.title }}</v-card-title>
              <v-card-subtitle 
                @click="isOpen = true" 
                class="chat__users"
              >
                {{ chatData.users.length }} Участников
              </v-card-subtitle>
              <v-spacer></v-spacer>
              <v-icon size="32" style="cursor: pointer;" @click="openSettings">
                settings
              </v-icon>
            </v-toolbar>
            <div class="chat__middle" v-for="(msg, index) of messageList" :key="index">
              <div 
                v-if="msg.authorId == user.id" 
                class="chat__my-msg d-flex flex-row"
              >
                <v-img 
                  :src="'/' + msg.author.additionally.img"
                  v-if="msg.author.additionally.img" 
                  style="margin-right: 20px" 
                  class="chat__middle__logo"
                ></v-img>
                <div v-else style="margin-right: 20px" class="chat__middle__logo"></div>
                <div>
                  <h4>{{ msg.author.firstname }} {{ msg.author.lastname }}</h4>
                  <p class="chat__from-me">{{ msg.text }}</p>
                </div>
                <v-icon 
                  @click="msgRemove(msg)"
                  class="chat__middle__icon"
                >
                  cancel
                </v-icon>
              </div>
              <div v-if="msg.authorId != user.id" class="chat__notmy-msg d-flex flex-row">
                <div>
                  <h4>{{ msg.author.firstname }} {{ msg.author.lastname }}</h4>
                  <p class="chat__from-them">{{ msg.text }}</p>
                </div>
                <v-img 
                  :src="'/' + msg.author.additionally.img"
                  v-if="msg.author.additionally.img" 
                  style="margin-right: 20px" 
                  class="chat__middle__logo"
                ></v-img>
                <div v-else style="margin-left: 20px" class="chat__middle__logo"></div>
              </div>
            </div>
            <div id="chatBottom" class="chat__bottom d-flex justify-center">
              <v-textarea 
                solo 
                auto-grow
                rows="1"
                label="Введите ваше сообщение..."
                class="chat__bottom__input"
                v-model="message"
              ></v-textarea >
              <v-btn large class="chat__bottom__btn" @click="sendMessage" color="myPurple">
                Отправить
              </v-btn>
            </div>
          </v-card>
          <!-- <ChatSettings :chatData="chatData" :isSettings="isSettings" /> -->
          <v-card id="settings" class="chat__settings chat__settings__close">
            <div class="d-flex flex-row align-center justify-space-between">
              <v-card-title>Настройки чата</v-card-title>
              <v-icon style="margin-right: 20px; cursor: pointer" @click="settingsClose">
                cancel
              </v-icon>
            </div>
            <div class="chat__settings__form d-flex flex-column align-center">
              <p class="chat__settings__title">Название</p>
              <h3 class="chat__settings__name" @click="isEditTitle = true" v-if="!isEditTitle">
                {{ chatData.title }}
              </h3>
              <v-text-field 
              class="chat__settings__input" 
              v-else 
              solo
              placeholder="Введите название чата"
              v-model="chatTitle"
              ></v-text-field> 
              <p class="chat__settings__title">Описание</p>
              <h3 
                class="chat__settings__description" 
                @click="isEditDesc = true" 
                v-if="!isEditDesc && chatData.description"
              >
                {{ chatData.description }}
              </h3>
              <v-textarea
                class="chat__settings__input" 
                v-if="isEditDesc || !chatData.description" 
                solo
                auto-grow
                rows="1"
                placeholder="Введите описание чата"
                v-model="chatDescription"
              >
              </v-textarea>
              <v-btn 
                @click="dataSave" 
                color="myPurple"
                large 
                style="color: #fff; margin-top: 30px; margin-bottom: 20px"
              >
                Сохранить
              </v-btn>
              <v-btn @click="chatLeave" color="error">Покинуть чат</v-btn>
            </div>
          </v-card>
        </div>
      </template>
    </NotAuth>
  </div>
</template>

<script>
import ChatUsers from "../components/chat/ChatUsers.vue";
// import ChatSettings from "../components/chat/ChatSettings.vue";
import NotAuth from "../components/NotAuth.vue";
import Panel from "../components/Panel.vue";
import axios from "axios";
import Elevator from "elevator.js";
import moment from "moment";
import "../scss/chat.scss";

export default {
  name: "Chat",
  metaInfo: {
    title: "MyOcean - ЧАТ"
  },
  components: { ChatUsers, NotAuth, Panel },
  data() {
    return {
      user: "",
      isOpen: false,
      chatData: "",
      messageList: [],
      message: "",
      isDelete: false,
      isSettings: false,
      elevator: "",
      users: [],
      isEditTitle: false,
      isEditDesc: false,
      chatTitle: "",
      chatDescription: ""
    }
  },
  async created() {
    this.user = JSON.parse(localStorage.getItem("user"))
    let res = await axios.get("/api/chat/get_chat/" + this.$route.params.id + "/" 
      + this.user.id)
    this.chatData = res.data
    this.$socket.client.emit("getMessages", this.$route.params.id)
    this.elevator = new Elevator({
      targetElement: document.getElementById('chatBottom'),
      element: document.querySelector('.chat__btn'),
      duration: 2000
    })
    this.chatTitle = this.chatData.title
    this.chatDescription = this.chatData.description
  },
  sockets: {
    connect: function() {
      console.log("юзер конэктэд")
    },
    getMessages: function(data) {
      this.messageList = data
    },
    messageAdd: function(data) {
      this.messageList = data
    }
  },
  methods: {
    sendMessage() {
      this.$socket.client.emit("sendMessages", { data: {
        text: this.message,
        authorId: this.user.id,
        chatId: this.$route.params.id,
        msgDate: moment().format("lll"),
        id: Math.floor(Math.random() * 99999999999999)
      }, chatId: this.$route.params.id})
      this.message = ""
    },
    msgRemove(msg) {
      this.$socket.client.emit("deleteMessage", { msg: msg.id, chatId: msg.chatId })
    },
    isModalFalse(data) {
      this.isOpen = data
    },
    openSettings() {
      this.isSettings = true
      let card = document.getElementById("settings")
      card.classList.remove("chat__settings__close")
    },
    settingsClose() {
      this.isSettings = false
      this.chatTitle = ""
      this.chatDescription = ""
      this.isEditTitle = false
      this.isEditDesc = false
      let card = document.getElementById("settings")
      card.classList.add("chat__settings__close")
    },
    async dataSave() {
      await axios.put("/api/chat/data-save", { chatId: this.chatData.chatId, 
        title: this.chatTitle, description: this.chatDescription })
      this.isEditTitle = false
      this.isEditDesc = false
      let card = document.getElementById("settings")
      card.classList.add("chat__settings__close")
    },
    async chatLeave() {
      await axios.delete("/api/chat/user-delete/" + this.user.id +
        "/" + this.chatData.chatId)
      this.$router.push({ name: 'Chats' })
    },
    dataEdit(data) {
      this.chatData = data
    }
  }
};
</script>
