<template>
  <div>
    <v-dialog persistent v-model="isOpen" max-width="700">
      <v-card class="chat__users">
        <div class="d-flex flex-row justify-space-between">
          <v-card-title class="chat__users__title">Пользователи</v-card-title>
          <v-icon @click="$emit('isModalFalse', false); isAdd = false" size="32">
            cancel
          </v-icon>
        </div>
        <div v-for="(user, index) of users" :key="index">
          <div class="chat__users__item d-flex flex-row">
            <v-img 
              v-if="user.additionally.img" 
              class="chat__users__logo"
              :src="'/' + user.additionally.img"
            >
            </v-img>
            <div v-else class="chat__users__logo"></div>
            <div class="chat__users__name">
              <router-link 
                class="chat__users__link" 
                :to="{ name: 'Account', params: { id: user.id }}"
              >
                {{ user.firstname }} {{ user.lastname }}
              </router-link>
            </div>
          </div>
        </div>
        <div>
          <v-btn @click="isAdd = true" class="chat__users__btn" color="success">
            Добавить пользователя
          </v-btn>
          <v-btn color="error" class="chat__users__btn" v-if="isAdd" @click="isAdd = false">
            <v-icon>cancel</v-icon>
          </v-btn>
        </div>
        <div v-if="isAdd" class="chat__users__edit">
          <v-text-field 
            solo 
            class="chat__users__input"
            placeholder="Введите название пользоватедя"
            v-model="fullname"
            @keydown.enter="search"
          >
          </v-text-field>
          <div v-if="dataList != []">
            <div 
              v-for="(data, index) of dataList" 
              :key="index"
            >
              <div 
                v-if="chatData.users.includes(data.id) != true" 
                style="margin-left: 20px; margin-top: 30px" 
                class="d-flex flex-row align-center"
              >
                <v-img 
                  v-if="data.additionally.img" 
                  class="chat__users__logo"
                  :src="'/' + user.additionally.img"
                >
                </v-img>
                <div v-else class="chat__users__logo"></div>
                <div class="d-flex flex-column">
                  <router-link 
                    style="font-size: 24px; margin-left: 20px"
                    class="chat__users__link" 
                    :to="{ name: 'Account', params: { id: data.id }}"
                  >
                    {{ data.firstname }} {{ data.lastname }}
                  </router-link>
                  <v-btn 
                    @click="userAdd(data)" 
                    style="color: #fff" 
                    class="chat__users__button" 
                    color="myPurple"
                  >
                    Добавить в чат
                  </v-btn>
                </div>
              </div>
            </div>
          </div>
        </div>
      </v-card> 
    </v-dialog>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "ChatUsers",
  data() {
    return {
      users: [],
      isAdd: false,
      fullname: "",
      firstname: "",
      lastname: "",
      dataList: []
    }
  },
  props: {
    chatData: Object,
    isOpen: Boolean,
  },
  async created() {
    let res = await axios.get("/api/users/find_friends/" + this.chatData.users)
    this.users = res.data
  },
  methods: {
    async search() {
      this.firstname = this.fullname.trimStart().split(' ')[0]
      this.lastname = this.fullname.trimEnd().split(' ')[1]
      let res = await axios.post("/api/users/get_friends", 
        { firstname: this.firstname, lastname: this.lastname })
      this.dataList = res.data
    },
    async userAdd(user) {
      await axios.put("/api/chat/add-user", { chatId: this.chatData.chatId, user: user.id })
      this.$emit('isModalFalse', false);
    }
  }
};
</script>

<style lang="scss" scoped>
.chat {
  &__users {
    padding: 20px;
    min-height: 300px;
    text-decoration: none !important;
    cursor: default;
    &__item {
      margin-top: 50px;
      margin-left: 20px;
      margin-bottom: 10px;
    } 
    &__logo {
      max-width: 64px;
      min-height: 64px;
      min-width: 64px;
      max-height: 64px;
      border-radius: 50%;
      background-color: #2cb67d;
      cursor: pointer;
    }
    &__name {
      margin-left: 20px;
      font-size: 24px;
    }
    &__link {
      text-decoration: none;
      &:hover {
        text-decoration: none !important;
      }
    }
    &__btn {
      margin-top: 30px;
      margin-left: 20px;
    }
    &__input {
      width: 600px;
      margin-top: 30px;
      margin-left: 20px;
    }
    &__button {
      margin-top: 10px;
      margin-left: 20px;
      width: 200px;
    }
  }
}
</style>
